"use client";

import { UniqueIdentifier } from "@dnd-kit/core";
import { DotsHorizontalIcon } from "@radix-ui/react-icons";

import * as React from "react";

import {
    AlertDialog,
    AlertDialogCancel,
    AlertDialogContent,
    AlertDialogDescription,
    AlertDialogFooter,
    AlertDialogHeader,
    AlertDialogTitle,
} from "@/Components/ui/alert-dialog";
import { Button } from "@/Components/ui/button";
import {
    DropdownMenu,
    DropdownMenuContent,
    DropdownMenuItem,
    DropdownMenuSeparator,
    DropdownMenuTrigger,
} from "@/Components/ui/dropdown-menu";
import { Input } from "@/Components/ui/input";
import { useToast } from "@/Components/ui/use-toast";

import { useTaskStore } from "@/lib/store";

export function ColumnActions({
    title,
    id,
}: {
    title: string;
    id: UniqueIdentifier;
}) {
    const [open, setIsOpen] = React.useState(false);
    const [name, setName] = React.useState(title);
    const updateCol = useTaskStore((state) => state.updateCol);
    const removeCol = useTaskStore((state) => state.removeCol);
    const [editDisable, setIsEditDisable] = React.useState(true);
    const [showDeleteDialog, setShowDeleteDialog] = React.useState(false);
    const inputRef = React.useRef<HTMLInputElement>(null);
    const { toast } = useToast();

    return (
        <>
            <form
                onSubmit={(e) => {
                    e.preventDefault();
                    setIsEditDisable(!editDisable);
                    updateCol(id, name);
                    toast({
                        title: "Name Updated",
                        variant: "default",
                        description: `${title} updated to ${name}`,
                    });
                }}
            >
                <Input
                    value={name}
                    onChange={(e) => setName(e.target.value)}
                    className="!mt-0 mr-auto text-base disabled:cursor-pointer disabled:border-none disabled:opacity-100"
                    disabled={editDisable}
                    ref={inputRef}
                />
            </form>
            <DropdownMenu modal={false}>
                <DropdownMenuTrigger asChild>
                    <Button variant="secondary" className="ml-1">
                        <span className="sr-only">Actions</span>
                        <DotsHorizontalIcon className="w-4 h-4" />
                    </Button>
                </DropdownMenuTrigger>
                <DropdownMenuContent align="end">
                    <DropdownMenuItem
                        onSelect={() => {
                            setIsEditDisable(!editDisable);
                            setTimeout(() => {
                                inputRef.current && inputRef.current?.focus();
                            }, 500);
                        }}
                    >
                        Rename
                    </DropdownMenuItem>
                    <DropdownMenuSeparator />

                    <DropdownMenuItem
                        onSelect={() => setShowDeleteDialog(true)}
                        className="text-red-600"
                    >
                        Delete Section
                    </DropdownMenuItem>
                </DropdownMenuContent>
            </DropdownMenu>
            <AlertDialog
                open={showDeleteDialog}
                onOpenChange={setShowDeleteDialog}
            >
                <AlertDialogContent>
                    <AlertDialogHeader>
                        <AlertDialogTitle>
                            Are you sure want to delete column?
                        </AlertDialogTitle>
                        <AlertDialogDescription>
                            NOTE: All tasks related to this category will also
                            be deleted.
                        </AlertDialogDescription>
                    </AlertDialogHeader>
                    <AlertDialogFooter>
                        <AlertDialogCancel>Cancel</AlertDialogCancel>
                        <Button
                            variant="destructive"
                            onClick={() => {
                                // yes, you have to set a timeout
                                setTimeout(
                                    () =>
                                        (document.body.style.pointerEvents =
                                            ""),
                                    100,
                                );

                                setShowDeleteDialog(false);
                                removeCol(id);
                                toast({
                                    description:
                                        "This column has been deleted.",
                                });
                            }}
                        >
                            Delete
                        </Button>
                    </AlertDialogFooter>
                </AlertDialogContent>
            </AlertDialog>
        </>
    );
}
